<template>
  <div class="sidebar">
    <div class="sideBox">
      <el-menu class="sidebar-el-menu" :default-active="onRoutes" :collapse="collapse" unique-opened router>
        <li v-for="item in items" class="mainMenu" :class="{hover : item.index == showMenuFlag || item.index == currentPageFlag}"
            @mouseover="showMenuBox(item.index)" @mouseout="hideMenuBox(item.index)">
          <i :class="item.icon"></i>
          <span v-if="!collapse">{{item.title}}</span>
          <div class="subMenu" :class="{collapse: collapse}" v-if="item.subs" v-show="item.index == showMenuFlag">
            <div class="subMenuBox" v-for="(subItem,index) in item.subs" :key="index">
              <h3 class="subMenu_title">{{subItem.title}}</h3>
              <router-link :to="lastMenu.router" class="subMenu_link" v-for="(lastMenu,index) in subItem.subs"
                           :key="index">{{lastMenu.title}}
              </router-link>
            </div>
          </div>
        </li>
      </el-menu>
    </div>
  </div>
</template>

<script>
  import bus from '../common/bus';

  export default {
    data() {
      return {
        collapse: false,
        showMenuFlag: '',
        currentPageFlag: '',
        items: [
          {
            icon: 'el-icon-tickets',
            title: '任务',
            index: 'taskManage',
            subs: [
              {
                title: '需求提交',
                subs: [
                  {
                    title: "需求列表",
                    router: "/taskManage/submitDemand"
                  }
                ]
              },
              {
                title: '审核',
                subs: [
                  {
                    title: "翻译审核",
                    router: "/taskManage/check"
                  }
                ]
              },
              {
                title: '任务列表',
                subs: [
                  {
                    title: "翻译任务",
                    router: "/taskManage/translateTask"
                  },
                  {
                    title: "设计任务",
                    router: "/taskManage/designTask"
                  }
                ]
              }
            ]
          },
          {
            icon: 'el-icon-document',
            index: 'order',
            title: '订单',
            subs: [
              {
                title: '订单管理',
                subs: [
                  {
                    title: "订单管理",
                    router: "/order/index"
                  },
                  {
                    title: "FB专用列表",
                    router: "/order/index"
                  }
                ]
              },
              {
                title: '订单审核',
                subs: [
                  {
                    title: "订单审核",
                    router: "/order/orderAudit"
                  }
                ]
              },
              {
                title: '退单统计',
                subs: [
                  {
                    title: "退单统计",
                    router: "/order/index"
                  }
                ]
              }
            ]
          },
          {
            icon: 'el-icon-goods',
            index: 'product',
            title: '产品',
            subs: [
              {
                title: '产品管理',
                subs: [
                  {
                    title: "产品检查",
                    router: "/product/check/index"
                  },
                  {
                    title: "产品列表",
                    router: "/product/index"
                  },
                  {
                    title: "发布产品列表",
                    router: "/product/release/index"
                  }
                ]
              },
              {
                title: '分类管理',
                subs: [
                  {
                    title: "分类列表",
                    router: "/product/category/index"
                  },
                ]
              },
              {
                title: '供应商管理',
                subs: [
                  {
                    title: "供应商列表",
                    router: "/product/supplier/index"
                  },
                ]
              },
              {
                title: '品牌管理',
                subs: [
                  {
                    title: "品牌列表",
                    router: "/product/brand/index"
                  },
                ]
              },
              {
                title: '属性管理',
                subs: [
                  {
                    title: "属性列表",
                    router: "/product/attr/index"
                  },
                ]
              },
            ]
          },
          {
            icon: 'el-icon-goods',
            index: 'finance',
            title: '财务',
            subs: [
              {
                title: '账单列表',
                subs: [
                  {
                    title: "导入账单",
                    router: "/finance/upload"
                  },
                  {
                    title: "核对账单",
                    router: "/product/add"
                  },
                  {
                    title: "导出账单",
                    router: "/product/check/add"
                  }
                ]
              },
              {
                title: '应收',
                subs: [
                  {
                    title: "COD收款单",
                    router: "/product/index"
                  },
                  {
                    title: "赔偿收款单",
                    router: "/product/add"
                  },
                ]
              },
              {
                title: '付款单',
                subs: [
                  {
                    title: "COD手续费",
                    router: "/product/index"
                  },
                  {
                    title: "头程费用",
                    router: "/product/add"
                  },
                  {
                    title: "尾程费用",
                    router: "/product/add"
                  },
                  {
                    title: "销售退款",
                    router: "/product/add"
                  },
                ]
              },
              {
                title: '费用计算',
                subs: [
                  {
                    title: "运费计算",
                    router: "/product/index"
                  },
                  {
                    title: "COD手续费计算",
                    router: "/product/add"
                  },
                  {
                    title: "退款手续费计算",
                    router: "/product/add"
                  },
                  {
                    title: "其它费用计算",
                    router: "/product/add"
                  },
                ]
              },
              {
                title: '导入',
                subs: [
                  {
                    title: "运费计算",
                    router: "/product/index"
                  },
                  {
                    title: "COD手续费计算",
                    router: "/product/add"
                  },
                  {
                    title: "退款手续费计算",
                    router: "/product/add"
                  },
                  {
                    title: "其它费用计算",
                    router: "/product/add"
                  },
                ]
              }
            ]
          },
          {
            icon: 'el-icon-tickets',
            index: 'report',
            title: '报表'
          },
          {
            icon: 'el-icon-tickets',
            title: '站点管理',
            index: 'config',
            subs: [
              {
                title: '站点管理',
                subs: [
                    {
                        title: "站点列表",
                        router: "/config/productList"
                    },
                  {
                    title: "地区列表",
                    router: "/config/salesCity"
                  },
                  {
                    title: "域名列表",
                    router: "/config/website"
                  },
                  {
                      title: "评论管理",
                      router: "/config/salesCity"
                  },
                ]
              },

            ]
          },
            {
                icon: 'el-icon-goods',
                index: 'caigou',
                title: '采购',
                subs: [
                    {
                        title: '采购管理',
                        subs: [
                            {
                                title: "采购单列表",
                                router: "/purchases/index"
                            },
                            {
                                title: "采购单汇总明细",
                                router: "/purchases-detail/index"
                            },
                            {
                                title: "采购付款单",
                                router: "/payments/index"
                            },
                            {
                                title: "采购收货单",
                                router: "/purchases-detail"
                            },
                            {
                                title: "收货异常处理",
                                router: "/product/release/add"
                            },

                        ]
                    },
                    {
                        title: '采购异常',
                        subs: [
                            {
                                title: "退货单列表",
                                router: "/pr-bill/index"
                            },
                            {
                                title: "退款单列表",
                                router: "/receivables/index"
                            },
                            
                        ]
                    }
                ]
            },


          {
            icon: 'el-icon-goods',
            index: 'pick',
            title: '发货',
            subs: [
              {
                title: '发货管理',
                subs: [
                  {
                    title: "拣货单",
                    router: "/pick-round/index"
                  },
                 
                  {
                    title: "打包复核",
                    router: "/pick-round/check"
                  },
                  {
                    title: "包裹尺寸录入",
                    router: "/pick-round/size"
                  },
                  {
                    title: "称重",
                    router: "/pick-round/weight"
                  },
                  {
                    title: "出库",
                    router: "/pick-round/out"
                  },
                  {
                    title: "异常单处理",
                    router: "/pick-round/problems"
                  }
                ]
              }
            ]
          },


          {
            icon: 'el-icon-goods',
            index: 'cangku',
            title: '仓库管理',
            subs: [
              {
                title: '自建仓管理',
                subs: [
                  {
                    title: "建仓管理",
                    router: "/warehouse/index"
                  },
                  {
                    title: "商品仓库情况",
                    router: "/inventory/index"
                  },
                  {
                    title: "库位库区管理",
                    router: "/sub-inventory/index"
                  }
                ]
              }, 
              {
                title: '转运仓',
                subs: [
                   {
                    title: "转运件",
                    router: "/stocks/index"
                  },
                  {
                    title: "我的转运件",
                    router: "/stocks/stock"
                  },
                  {
                    title: "待出库列表",
                    router: "/stocks/outbound"
                  },
                  {
                    title: "已出库列表",
                    router: "/stocks/outbound-list"
                  }
                ]
              }
            ]
          },

          {
            icon: 'el-icon-goods',
            index: 'problems',
            title: '问题单',
            subs: [
              {
                title: '问题单管理',
                subs: [
                  {
                    title: "问题单列表",
                    router: "/problems/index"
                  },
                   
                ]
              }
            ]
          },


        ]
      }
    },
    computed: {
      onRoutes() {
        return this.$route.path.replace('/', '');
      }
    },
    methods: {
      showMenuBox(val) {
        this.currentPageFlag = '';
        this.showMenuFlag = val;
      },
      hideMenuBox(val) {
        this.showMenuFlag = '';
        this.hoverMenu(this.$route);
      },
      hoverMenu(route) {
        let path = route.path;
        let secondIndex = path.indexOf('/',1);
        this.currentPageFlag = path.slice(1,secondIndex);
      }
    },
    created() {
      this.hoverMenu(this.$route);
      // 通过 Event Bus 进行组件间通信，来折叠侧边栏
      //bus.$on('collapse', msg => {
      //    this.collapse = msg;
      //})
    },
    watch: {
      $route(newValue,oldValue){
        this.hoverMenu(newValue);
      }
    }
  }
</script>

<style scoped>
  .sidebar {
    display: block;
    position: absolute;
    left: 0px;
    top: 60px;
    bottom: 0;
    overflow-y: scroll;
    width: 100%;
  }

  .sidebar::-webkit-scrollbar {
    width: 0;
  }

  .sidebar-el-menu {
    box-sizing: border-box;
  }

  .sidebar-el-menu:not(.el-menu--collapse) {
  }

  .sideBox {
    height: 100%;
  }

  .sideBox > ul {
    height: 100%;
    padding-top: 30px;
  }

  .mainMenu {
    position: relative;
    padding: 20px 28px 20px 45px;
    text-align: left;
    white-space: nowrap;
  }

  .mainMenu i {
    font-size: 20px;
    margin-right: 10px;
  }

  .mainMenu span {
    font-size: 16px;
    vertical-align: top;
  }

  .mainMenu .subMenu {
    z-index: 9999;
    display: flex;
    white-space: nowrap;
    position: absolute;
    top: 0;
    left: 180px;
    border: 1px solid #ccc;
    padding: 30px 0;
    background-color: white;
    box-shadow: 6px 6px 3px #888888;
  }

  .mainMenu.hover {
    cursor: pointer;
    color: #fff;
    background: #03B8CC;
  }

  .el-menu--collapse {
    width: 74px;
  }

  .subMenu.collapse {
    left: 74px;
  }

  .subMenuBox {
    padding: 0 30px;
    border-right: 1px solid #ccc;
  }

  .subMenuBox:last-child {
    border-right: none;
  }

  .subMenu_title {
    font-weight: normal;
    color: #93a6b7;
    font-size: 14px;
    text-align: center;
  }

  .subMenu_link {
    display: block;
    margin: 15px 0;
    font-size: 14px;
    color: #2c3e50;
    text-align: center;
  }
</style>



