import Main from "../components/common/Home.vue"

// 不作为Main组件的子页面展示的页面单独写，如下
export const defaultRouter = {
  path: '/',
  redirect: '/taskManage/submitDemand'
};

export const loginRouter = {
  path: '/login',
  name: 'login',
  meta: {
    title: 'Login - 登录'
  },
  component: () => import('page/Login.vue')
};

export const page404 = {
  path: '/*',
  name: 'error-404',
  meta: {
    title: '404-页面不存在'
  },
  component: () => import('page/error-page/404.vue')
};

export const page403 = {
  path: '/403',
  meta: {
    title: '403-权限不足'
  },
  name: 'error-403',
  component: () => import('page/error-page/403.vue')
};

// 任务模块路由
export const taskRouter = [
  //在菜单显示部分
  {
    path: '/taskManage',
    component: Main,
    meta: {title: '任务'},
    children: [
      {
        path: 'submitDemand',
        component: resolve => require(['../components/page/oms_taskManage/submitDemand.vue'], resolve),
        meta: {title: '需求列表'}
      },
      {
        path: 'check',
        component: resolve => require(['../components/page/oms_taskManage/check.vue'], resolve),
        meta: {title: '审核'}
      },
      {
        path: 'translateTask',
        component: resolve => require(['../components/page/oms_taskManage/translateTask.vue'], resolve),
        meta: {title: '翻译任务'}
      },
      {
        path: 'designTask',
        component: resolve => require(['../components/page/oms_taskManage/designTask.vue'], resolve),
        meta: {title: '设计任务'}
      }
    ]
  },
  //不在菜单显示部分
  {
    path: '/taskManage',
    component: Main,
    children: [
      {
        path: 'addTask',
        name: 'addTask',
        component: resolve => require(['page/oms_taskManage/addTask.vue'], resolve),
        meta: {title: '新增需求'}
      },
      {
        path: 'updateTask',
        name: 'updateTask',
        component: resolve => require(['page/oms_taskManage/updateTask.vue'], resolve),
        meta: {title: '修改需求'}
      },
      {//审核查看任务详情
        path: 'checkTaskInfo',
        component: resolve => require(['page/oms_taskManage/checkTaskInfo.vue'], resolve),
        meta: {title: '任务详情'}
      }
    ]
  }
]


